  text-align: center;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px 25px;
  max-width: 450px;
  min-width: 290px;
  z-index: 100000;
  border: 1px solid #ededed;
  box-shadow: 0 0 5px 3px rgba(186, 186, 186, 0.32);
}

